.loader {
  width: 32px;
  height: 32px;
  color: #ff3d00;
  border-radius: 50%;
  animation: fill 1s ease-in infinite alternate;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  animation: fill .9s ease-in infinite alternate;
  position: absolute;
  top: 0;
  left: 48px;
}

.loader:after {
  animation-duration: 1.1s;
  left: auto;
  right: 48px;
}

@keyframes fill {
  0% {
    box-shadow: inset 0 0 0 2px;
  }

  100% {
    box-shadow: inset 0 0 0 10px;
  }
}

.cat-info {
  max-width: 500px;
  text-align: center;
  background-color: #afeeff;
  border-radius: 8px;
  margin: 50px auto;
}

.cat-img {
  width: 100%;
  max-height: 300px;
  object-fit: cover;
  border-radius: 8px;
}

.cat-name {
  margin-top: 16px;
  font-size: 24px;
  font-weight: bold;
}

.cat-description {
  margin-top: 8px;
  font-size: 16px;
}

.cat-temperament {
  color: gray;
  margin-top: 8px;
  font-size: 14px;
}

